import React from 'react'
import {useFormik} from 'formik'
import * as Yup from 'yup'
function Register(){
    const Regex1=RegExp('^[2-9]{2}[0-9]{8}$')
   
    const initialValues={            
        Name:'',
          Email:'',
          Number:'',
          Gender:'',
          Password:'',
          CnfPass:'',
          
      }
   const   onSubmit=value=>{
          console.log(value)
      }
          const validationSchema= Yup.object({
          Name:Yup.string().required('Required!'),
          Email:Yup.string().email('Invalid Email format').required('Required!'),
          Number:Yup.string().required('Required!').matches(Regex1,'Enter 10 digit number'),
          Gender:Yup.string().required('Required!'),
          Password:Yup.string().required('Required!'),
          CnfPass:Yup.string().required('Required!').oneOf([Yup.ref('Password'),null],'both password should match')
      })
      const formik=useFormik({
        initialValues,
        onSubmit,    //ES6 short hand syntax is used here for object literals
        validationSchema
     })
    
     return (
         <div className="common">
         <form onSubmit={formik.handleSubmit} >
               
        
                <div className="form_field">
                    <label htmmlFor="Name" className="labl">
                        Name
                        </label>
            <input type="text" id="name" name="Name" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Name} 
            onBlur={formik.handleBlur}/>
            {formik.touched.Name && formik.errors.Name?
            <div className="errors_field">
                {formik.errors.Name}
                </div>
                :null}
            </div>
            <div clasName="form_field">
                <label htmlFor="Email" className="labl">
                    Email
                    </label>
            <input type="email"  id="email" name="Email" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Email}
             onBlur={formik.handleBlur}/>
            { formik.touched.Email && formik.errors.Email?
            <div className="errors_field">
                {formik.errors.Email}
                </div>
                : null}
                </div>
                <div clasName="form_field">
                <label htmlFor="Number" className="labl">
                    Number
                    </label>
            <input type="text"  id="number" name="Number" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Number}
             onBlur={formik.handleBlur}/>
            { formik.touched.Number && formik.errors.Number?
            <div className="errors_field">
                {formik.errors.Number}
                </div>
                : null}
                </div>
                <div clasName="form_field">
                <label htmlFor="Gender" className="labl">
                    Gender
                    </label>
            <input type="text"  id="gender" name="Gender" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Gender}
             onBlur={formik.handleBlur}/>
            { formik.touched.Gender && formik.errors.Gender?
            <div className="errors_field">
                {formik.errors.Gender}
                </div>
                : null}
                </div>
                <div clasName="form_field">
                <label htmlFor="Pass" className="labl">
                    Password
                    </label>
            <input type="text"  id="password" name="Password" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Password}
             onBlur={formik.handleBlur}/>
            { formik.touched.Password && formik.errors.Password?
            <div className="errors_field">
                {formik.errors.Password}
                </div>
                : null}
                </div>
                <div clasName="form_field">
                <label htmlFor="CnfPass" className="labl">
                    Confirm Password
                    </label>
            <input type="text"  id="cnfpass" name="CnfPass" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.CnfPass}
             onBlur={formik.handleBlur}/>
            { formik.touched.CnfPass && formik.errors.CnfPass?
            <div className="errors_field">
                {formik.errors.CnfPass}
                </div>
                : null}
                </div>
        
 
            <button type="submit" className='loginbutton'>
                REGISTER
                </button>       
            
            </form> 
         </div>
     )
     }
export default Register

