import React from 'react'
import axios from 'axios'
import {useFormik} from 'formik'
import * as Yup from 'yup'
import './Login.css'
import { useHistory } from 'react-router';

function Login(){
  const history=useHistory()
    const initialValues={            
         Email:'',
          Password:'',
          
        
    }
   const   onSubmit=value=>{
    axios.post('https://reqres.in/api/login',{"email":value.Email,"password":value.Password},
    { headers:{
      "Content-type":"Application/json"
    }}   )
 .then(Res=>{
   const Token=Res.data.token
   localStorage.setItem("token",Token)
   const RetrievedToken=localStorage.getItem("token")
   console.log(RetrievedToken)
   history.push('/LoggedIn/')
   window.location.reload()
  
 }).catch(Error=>{
  alert("Invalid username or password")
})
      }
      const validationSchema= Yup.object({
          Password:Yup.string().required('Required!'),
          Email:Yup.string().email('Invalid Email format').required('Required!'),
          
      })
      const formik=useFormik({
        initialValues,
        onSubmit,   
        validationSchema
     })
     return (
         <div className="common">
         <form onSubmit={formik.handleSubmit} >
               
         <div clasName="form_field">
                <label htmlFor="email" className="labl">
                    E-mail
                    </label><br></br>
            <input type="email"  id="email" name="Email" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Email}
             onBlur={formik.handleBlur}/>
            { formik.touched.Email && formik.errors.Email?
            <div className="errors_field">
                {formik.errors.Email}
                </div>
                : null}
                </div>
                <div className="form_field">
                    <label htmmlFor="password" className="labl">
                        Password
                        </label><br></br>
            <input type="text" id="password" name="Password" className="inpt" 
            onChange={formik.handleChange} 
            value={formik.values.Password} 
            onBlur={formik.handleBlur}/>
            {formik.touched.Password && formik.errors.Password?
            <div className="errors_field">
                {formik.errors.Password}
                </div>
                :null}
            </div>
        
 
            <button type="submit" onSubmit={onSubmit} className='loginbutton' >
                LOGIN
                </button>       
            
            </form> 
           
         </div>
     )
     }
export default Login

