import './Cont.css'
import Login from './Login'
import Register from './Register'
import { useHistory } from 'react-router';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  withRouter,Link
} from "react-router-dom";


function Cont(){



    return(
       <Router>
            <div className='container'>
            <Link to='/' className='lbutton'>Login</Link><span> </span><Link to='/Register' className='rbutton'>Register</Link>
            <Switch>
           <Route path='/Register'>
            <Register/>
          </Route>
          <Route  path='/'>
            <Login/>
          </Route>
            </Switch>
            
            </div>
       </Router>
    )
}
export default Cont