

import {
  BrowserRouter as Router,
  Switch,
  Route,
  withRouter
} from "react-router-dom";
import Home from "./components/Home"
import LoggedIn from "./components/LoggedIn";

function App() {

 
  return (
  
      <div>
       
     
     <Router>
     <Switch>
          <Route exact path='/'>
            <Home/>
          </Route>
          <Route path='/LoggedIn/'>
            <LoggedIn/>
          </Route>
        </Switch>
     </Router>
      
       
      </div>
  );
}

export default withRouter(App);
